export default {
	data() {
		return {
			url: ''
		}
	},
	methods: {
		ViewImage(e) {
			uni.previewImage({
				urls: [this.url],
				current: this.url
			});
		},
		showToast(title) {
			uni.showModal({
				title: '提示',
				content: title,
				showCancel: false,
				success: function(res) {
					if (res.confirm) {}
				}
			});
		},
		getData() {
			console.log(this.$store.state.userInfo)
			uni.showLoading({
				title:'加载中。。。',
				mask:true
			})
			var self = this;
			self.showPerson = false;
			uni.request({
				url: self.$store.state.urlPrefix + "/Web/Get_Web_BuildersInfo",
				data:{
					builderId: self.$store.state.userInfo.BuilderId
				}
			}).then(data => {
				var [error, res] = data;
				console.log(res.data);
				uni.hideLoading();
				if (res.data.code == 0) {
					self.url = res.data.data.QRCode;
				} else {
					self.showToast('获取失败！')
				}
			})
		},
	},
	mounted() {
		this.getData();
	}
}
